<template>
<div class="grid grid-cols-1 md:grid-cols-2 w-full text-white">
  <div class="absolute font-mono text-xs top-16 left-1/2">
    Username : admin
    Password : admin123 
  </div>
    <div class="md:w-full md:max-w-none max-w-sm mx-auto h-screen">
        <div class="flex justify-center md:justify-start ">
            <a href="#" class="capitalize px-4 py-2 mt-2 ml-2 text-xl font-bold text-white bg-black">
                login
            </a>
        </div>
        <div class="grid place-items-center w-full h-screen">
            <div class="w-full mx-9 md:mx-0 md:w-1/2">
              <div class="p-5 text-5xl border border-gray-300 text-black mb-4 flex justify-center rounded-md">
                <i class="fa-solid fa-lock filter drop-shadow-lg"></i>
              </div>
              <p class="text-3xl text-gray-900 text-center mb-2">
                  Welcome.
              </p>
              <div class="p-3 rounded-md bg-gray-100 border border-gray-300">
                  <div class="flex gap-2">
                    <div class="text-3xl text-gray-800 rounded-md mt-2"><i class="fa-solid fa-user"></i></div>
                    <v-text-field
                      v-model="username"
                      label="Username / Email"                  
                    >
                    </v-text-field>
                  </div>
                  <div class="flex gap-2">
                    <div class="text-3xl text-gray-800 rounded-md mt-2"><i class="fa-solid fa-lock filter drop-shadow-lg"></i></div>
                    <v-text-field
                      label="Password"
                      v-model="password"
                      :append-icon="show1 ? 'mdi-eye' : 'mdi-eye-off'"
                      :rules="[rules.req, rules.min]"
                      :type="show1 ? 'text' : 'password'"
                      @click:append="show1 = !show1"
                    >
                    </v-text-field>
                  </div>
              </div>
              <v-checkbox
                v-model="checkbox"
                :label="`Remember Me`"
                color="blue lighten-3"
                value="blue lighten-3"
                class="text-blue-600"
              ></v-checkbox>
              <v-btn 
                @click="log"
                class="w-full mt-4 px-4 py-2 text-base font-semibold text-center transition-all duration-200 ease-in bg-gray-900 shadow-md hover:text-black hover:bg-white focus:outline-none focus:ring-2 "
                :disabled="carel"
              >
                  <span class="w-full uppercase">
                      login
                  </span>
              </v-btn>
            </div>
        </div>
    </div>
    <div class="absolute md:relative shadow-2xl">
        <img class="hidden object-cover w-full h-screen md:block" src="@/assets/login.jpg"/>
    </div>
</div>
</template>
<script>
export default {
  data(){
    return {
      username : '',
      password: '',
      checkbox : false,
      show1 : true,
      carel : false,
      rules : {
        req : value => !!value || 'Password Required.',
        min : (v) => {
          if (v.length >= 8){
            this.carel = false
          }else{
            this.carel = true
            return 'min 8 characters'
          }
        },
      },
    }
  },
  methods: {
    log(){
      const z = localStorage.getItem("admin")
      const zk = JSON.parse(z)
        if (this.username.length > 0 && this.password.length >= 8) {
          if (this.password == zk[0].password && this.username == zk[0].username) {
            var m = [
              {
                username : this.username,
                password : this.password,
              }
            ]
            sessionStorage.setItem("admin", JSON.stringify(m))
            setTimeout(() => {
              this.$router.push({name : 'Dashboard'})
            }, 300)
          }else{
            alert("false")
          }
        }else{
          alert("Username or Password  wrong")
        }
      },
  },
  created(){
    const admin = [
      {
        username : 'admin',
        password : 'admin123'
      }
    ]
    localStorage.setItem("admin", JSON.stringify(admin))
  }
}
</script>
